/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { Menu, Icon, Badge } from 'antd';
import axios from 'axios';
import { USER_SERVER } from '../../../Config';
import { withRouter } from 'react-router-dom';
import { useSelector } from "react-redux";

function RightMenu(props) {
  const user = useSelector(state => state.user)

  const logoutHandler = () => {
    axios.get(`${USER_SERVER}/logout`).then(response => {
      if (response.status === 200) {
        props.history.push("/login");
      } else {
        alert('Log Out Failed')
      }
    });
  };

  if (user.userData && !user.userData.isAuth) {
    return (
      <Menu mode={props.mode}>
        <Menu.Item key="mail" style={{ backgroundColor: '#282c34' }} >
          <a href="/login" style={{ color:'white'}}><b>Signin</b></a>
        </Menu.Item>
        <Menu.Item key="app" style={{ backgroundColor: '#282c34' }}>
          <a href="/register" style={{ color:'white'}}><b>Signup</b></a>
        </Menu.Item>
      </Menu>
    )
  } else {
    return (
      <Menu mode={props.mode}>

        <Menu.Item key="history" style={{ backgroundColor: '#282c34' }}>
          <a href="/history" style={{ color:'white'}}><b>History</b></a>
        </Menu.Item>

        <Menu.Item key="upload" style={{ backgroundColor: '#282c34' }}>
          <a href="/product/upload" style={{ color:'white'}}><b>Upload</b></a>
        </Menu.Item>

        <Menu.Item key="cart" style={{ paddingBottom: 3,  backgroundColor: '#282c34' }}>
          <Badge count={user.userData && user.userData.cart.length} >
            <a href="/user/cart" style={{ marginRight: -22 , color:'white'}}>
              <Icon type="shopping-cart" style={{ fontSize: 30, marginBottom: 3 }} />
            </a>
          </Badge>
        </Menu.Item>


        <Menu.Item key="logout" style={{ backgroundColor: '#282c34' }}>
          <a onClick={logoutHandler} style={{ color:'white'}}><b>Logout</b></a>
        </Menu.Item>
      </Menu>
    )
  }
}

export default withRouter(RightMenu);

